
import { computed, onMounted, reactive } from "vue";
const dataList = require("@/json/getTechnologys.json");

export default {
  setup() {
    /*****************  数据声明  ***************/
    const technologys = computed(() => {
      return dataList.result;
    });
    /*****************  生命周期  ***************/

    /*****************  自定义方法  ***************/
    const getIcon = (icon: string) => {
      if (icon) {
        return icon;
      }
      return require("@/assets/logo.png");
    };

    /*****************  网络请求  ***************/

    return {
      technologys,
      getIcon,
    };
  },
};
